:root {
    --dark1: #21295C;
    --dark2: #1B3B6F;
    --dark3: #065A82;
    --dark4: #1C7293;
    --light1: #9EB3C2;
    --light2: #71B48D;
    --text1: #EEF4ED;
    --text2: #EEF4ED;
}


.App {
    text-align: center;
}

.App-header {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: stretch;
    justify-content: center;
    color: var(--text1);
}

.App-header h1 {
    letter-spacing: 6px;
    font-size: 4.5rem;
    font-weight: 900;
    font-variant: small-caps;
    padding: 0;
    margin: 0;
}

.App-header p {
    line-height: 1.3;
    font-size: 1.3rem;
}

.App-content {
    padding: 1.5rem 15rem 3rem 15rem;
    background: rgba(0, 0, 0, 0.4);
}

.App-buttons {
    display: flex;
    flex: 1;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    
    padding-top: 1rem;
}

.App-link-button {
    padding: 1rem 1.5rem 1rem 1.5rem;
    margin: 0rem 1.5rem 0rem 1.5rem;

    font-family: 'Poppins';
    font-size: 1.3rem;
    color: var(--text1);
    background: rgba(0, 0, 0, 0.4);
    border: none;
    transition: 0.3s ease-in-out 0s;
}

.App-link-button:hover {
    background: rgba(0, 0, 0, 0.8);
}

@keyframes gradient {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}

#tsparticles {
    position: fixed;
    height: 100%;
    width: 100%;
    margin: 0;
    padding: 0;
    left: 0;
    top: 0;
    z-index: -1;
    background: linear-gradient(-45deg, var(--dark2), var(--dark3), var(--dark2), var(--dark3));
    background-size: 400% 400%;
    animation: gradient 15s ease infinite;
    height: 100vh;
}
